body {
  font-family: sans-serif;
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  font-family: "HeliosLight";
  overflow-x: hidden;
  margin: 0;
  padding: 0; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a:active,
a:visited,
a:focus {
  text-decoration: none; }

.js-yourname {
  font-weight: 700; }

.toggle-mnu {
  display: none;
  width: 28px;
  height: 28px;
  margin-top: 5px;
  position: absolute;
  top: 0;
  left: 30px;
  z-index: 100; }
  @media only screen and (max-width: 768px) {
    .toggle-mnu {
      display: block; } }
  .toggle-mnu span {
    position: relative;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
    .toggle-mnu span:after, .toggle-mnu span:before {
      content: "";
      position: absolute;
      left: 0;
      top: 9px; }
    .toggle-mnu span:after {
      top: 18px;
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
    .toggle-mnu span:before {
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
  .toggle-mnu.on span {
    background-color: transparent; }
    .toggle-mnu.on span:before {
      transform: rotate(45deg) translate(-1px, 0px); }
    .toggle-mnu.on span:after {
      transform: rotate(-45deg) translate(6px, -7px); }
